<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                我的认证</p>
        </section>
        <div class="con-con">
            <md-tabs>
                <md-tab-pane class="content" name="p1" label="基本信息">
                    <div class="info-content">
                        <md-cell-item title="姓名" :addon="userInfo.name"/>
                        <md-cell-item title="身份证号" :addon="userInfo.idCard"/>
                        <md-cell-item title="性别" :addon="userInfo.gendar"/>
                        <md-cell-item title="出生日期" :addon="userInfo.birthDate"/>
                        <md-cell-item title="毕业院校" :addon="userInfo.graduationSchool"/>
                        <md-cell-item title="学历" :addon="userInfo.education"/>
                        <md-cell-item title="学位" :addon="userInfo.academic"/>
                        <md-cell-item title="专家类别" :addon="userInfo.type"/>
                        <md-cell-item title="研究领域" :addon="userInfo.genre"/>
                    </div>
                    <div class="info-pic">
                        <div class="card">
                            <span>手持身份证照</span>
                            <img class="card-photo" :src="userInfo.idCardImg" alt="">
                        </div>
                        <div class="photo">
                            <span>免冠照片</span>
                            <img class="recard-photo" :src="userInfo.pic" alt="">
                        </div>
                    </div>
                </md-tab-pane>
                <md-tab-pane class="content" name="p2" label="联系信息">
                    <div class="info-content">
                        <md-cell-item title="工作单位" :addon="userInfo.unit"/>
                        <md-cell-item title="工作部门" :addon="userInfo.department"/>
                        <md-cell-item title="现任职务" :addon="userInfo.post"/>
                        <md-cell-item title="手机号" :addon="userInfo.mobile"/>
                        <md-cell-item title="办公电话" :addon="userInfo.phone"/>
                        <md-cell-item title="邮箱" :addon="userInfo.email"/>
                        <md-cell-item title="通讯地址" :addon="userInfo.address"/>
                    </div>
                </md-tab-pane>
                <md-tab-pane class="content" name="p3" label="其他信息">
                    <div class="other-content">
                        <div class="one">
                            <div class="title">主要成果（学术成就/研究成果/管理成果等）</div>
                            <div class="des">{{ userInfo.achievements }}</div>
                        </div>
                        <div class="one">
                            <div class="title">社会荣誉和行业资质</div>
                            <div class="des">{{ userInfo.socialHonor }}</div>
                        </div>
                        <div class="one">
                            <div class="title">典型案例（产学研合作项目经验等）</div>
                            <div class="des">{{ userInfo.classicCase }}</div>
                        </div>
                    </div>
                    <div class="enclosure-con">
                        <div class="title">附件</div>
                        <div class="des">{{ userInfo.fileName }}</div>
                    </div>
                    <div class="footer">*支持在电脑端重新填写认证信息</div>
                </md-tab-pane>
            </md-tabs>
        </div>
    </main>
</template>

<script>
import { Field, CellItem, Icon, InputItem, Button, Tabs, TabPane } from 'mand-mobile'
import { getInfo } from '../../../apiUrl/personal/cert'
export default {
  name: 'index',
  components: {
    [Field.name]: Field,
    [CellItem.name]: CellItem,
    [Icon.name]: Icon,
    [Button.name]: Button,
    [Tabs.name]: Tabs,
    [TabPane.name]: TabPane,
    [InputItem.name]: InputItem
  },
  data () {
    return {
      userInfo: {}
    }
  },
  mounted () {
    this.getInfo()
  },
  methods: {
    // 回退
    goBack () {
      this.$router.go(-1)
    },
    getInfo () {
      getInfo().then((res) => {
        console.log(res.data.status === 2)
        if (res.data.status === 2) {
          this.userInfo = res.data
        } else {
          this.$router.replace('/personal/noCert')
        }
      })
    }
  }
}
</script>

